.newsContainer {
   
  }
  .mainHeader{
    display: flex;
    justify-content: center;
    padding-top: 15px;
    
  }
  .titleContainer{
    padding-left: 5px;
  }
  .newsList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .newsItem {
    margin: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: calc(33.33% - 20px);
  }
  
  .newsImage {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 10px;
    
  }
  
  .newsTitle {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    padding-top: 10px;
  }
  
  .newsText {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .newsLink {
    text-decoration: none;
    color: #337ab7;
    transition: color 0.2s ease;
  }
  
  .newsLink:hover {
    color: #23527c;
  }